.banner {
    position: relative;
    background: url(../img/banner_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding-bottom: 80px;
    &-item {
        max-width: 1400px;
        margin-left: auto;
        margin-right: auto;
        &__lead {
            padding-top: 87px;
            display: flex;
            flex-direction: row;
            p {
                span {
                    background-color: #9cafc2;
                    color: white;
                    font-size: 22px;
                    font-weight: bold;
                    padding: 3px 10px;
                    border-radius: 5px;
                    letter-spacing: 1px;
                }
            }
            p:nth-child(1) {
                margin-right: 20px;
            }
        }
        &__ttl {
            margin-top: 40px;
            h1 {
                -webkit-background-clip: text;
                background-image: linear-gradient(to right, #004183, #0eb49a);
                -webkit-text-fill-color: transparent;
                color: #464646;
                display: inline-block;
                font-size: 70px;
                line-height: (90 / 70);
                font-weight: bolder;
            }
        }
        &__check {
            margin-top: 10px;
            p {
                margin-top: 10px;
                span {
                    position: relative;
                    font-size: 22px;
                    font-weight: bold;
                    margin-left: 34px;
                    &::before {
                        content: "";
                        background: url(../img/check_icon.png) no-repeat;
                        background-size: cover;
                        width: 24px;
                        height: 18px;
                        position: absolute;
                        top: 5px;
                        left: -34px;
                    }
                    &::after {
                        content: "";
                        border-bottom: 1px solid black;
                        position: absolute;
                        width: 100%;
                        left: 0;
                        bottom: 0px;
                    }
                }
            }
        }
        &__ranking {
            display: flex;
            flex-direction: row;
            margin-top: 30px;
            &-col {
                position: relative;
                margin-right: 20px;
                &:last-child {
                    margin-right: unset;
                }
                p {
                    position: absolute;
                    font-size: 30px;
                    font-weight: bold;
                    top: 60px;
                    width: 172px;
                    text-align: center;
                }
            }
        }
    }
}

.contact {
    position: relative;
    background: url(../img/contact_bg1.jpg) no-repeat;
    background-size: cover;
    background-position: center;
    padding: 55px 0 45px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    &-item {
        text-align: center;
        p {
            font-size: 24px;
            font-weight: bold;
            color: white;
            span {
                &::before {
                    content: "╲";
                    font-size: 24px;
                    color: white;
                    display: inline-block;
                    margin-right: 15px;
                }
                &::after {
                    content: "╱";
                    font-size: 24px;
                    color: white;
                    display: inline-block;
                    margin-left: 15px;
                }
            }
        }
        button {
            margin-top: 20px;
            width: 700px;
            height: 100px;
            background-color: #f5ff00;
            border: unset;
            position: relative;
            border-radius: 50px;
            box-shadow: 0px 8px rgba(0, 0, 0, 0.185);
            span {
                font-size: 24px;
                font-weight: bold;
                line-height: (24 / 24);
                &::before {
                    content: "";
                    width: 39px;
                    height: 27px;
                    background: url(../img/contact_icon_mail.png) no-repeat;
                    background-size: cover;
                    display: inline-block;
                    margin-right: 20px;
                    vertical-align: bottom;
                }
            }
            &::after {
                content: "";
                background: url(../img/arrow_right.png) no-repeat;
                background-size: cover;
                width: 37px;
                height: 37px;
                position: absolute;
                top: calc(50% - 18.5px);
                right: 40px;
            }
            &:hover {
                transform: translate(0, 8px);
                box-shadow: none;
                opacity: 0.9;
            }
        }
    }
}

.intro {
    position: relative;
    margin-top: 100px;
